<template>
	<div class="community_item">
    <div class="row">
      <div class="col-sm-2">
        <img id="avatar" src="data:image/jpeg;base64," alt="Avatar">
      </div>
      <div class="col-sm-10">
        <router-link :to="{ name: 'communityDetails', params: { id: community._id }}"><h5>{{ community.communityName }}</h5></router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['community']
}
</script>

<style scoped>

  a.btn {
    margin-left: 8px;
  }
  a.btn i {
    font-size: 1em;
  }
  #avatar {
    width: 100%;
    border: 3px solid #fff;
    border-radius: 5px;
  }

  a {
    cursor: pointer;
    color: white;
  }

  h5 {
    display: inline-block;
    margin-bottom: 15px;
  }

  a:hover h5{
    text-decoration: underline;
  }

  .details i {
    font-size: 1.2em;
    color: #ddd;
    margin-right: 4px;
  }

  ul.details {
    /* background: #222; */
    padding: 10px;
    padding-left: 20px;
    border-radius: 3px;
    flex-wrap: wrap;
  }
  .details li {
    padding: 2px 0;
    list-style-type: none;
    font-size: 0.9em;
    flex-basis: calc(48%);
  }

</style>



